	module.exports = {"data":{"markdownRemark":{"html":"<p>Players form two lines of equal length. Mix it up so players are not across from someone they've done senes with already.\nTwo person scene starts with the following sequence steps:</p>\n<ol>\n<li>Person #1 starts the scene with an action/activity.</li>\n<li>Person #1 does not talk until Person #2 has spoken.</li>\n<li>Person #2 comes on and joins Person #1 in the environment created by their actions.</li>\n<li>Person #2 should be complimenting the actions of Person #1, heightening as well.</li>\n<li>Person #2 then says something about what they observe about Person #1.</li>\n<li>Person #1 then says something about what they observe about Person #2.</li>\n</ol>\n<p>The scene continues focusing on these relationships.\nPlayers switch lines when he/she has completed a scene. Continue until every player has completed a scene from both lines.</p>\n<p>Be sure to give these scenes time to breathe.\nIt's not uncommon for these scenes to last five minutes or more.\nIf the scene hasn't found a nature close we'll cut at around ten minutes for the sake of time.</p>","frontmatter":{"title":"Say What You See","type":"Exercise","foci":["Environment","Relationship","Observation"],"minimumPeople":2,"duration":10,"durationType":"linear","peoplePerScene":null}}},"pathContext":{"slug":"/say-what-you-see/"}}

